import React, {useState} from 'react';
import DatePicker from "react-datepicker";

export const Form = ({}) => {

    const [startDate, setStartDate] = useState(new Date());

    return (
        <div className="individual">
            <div className="container formCont">
                <header>
                    <h3>This Form is for Corporate organizations</h3>
                </header>
                <p id="note"><strong>Fields marked with <span>*</span> are required</strong></p>
                <form>
                    <div className="form-group">
                        <label for="fullname">Organizations' Name <span>*</span></label>
                        <input type="text" className="form-control" id="fullname" aria-describedby="fullname" />
                        <small id="fullname" className="form-text text-muted">Full Name (Surname First).</small>
                    </div>
                    <div className='row'>
                        <div className="form-group col-sm-6">
                            <label for="dob">Date of Incorporation <span>*</span></label>
                            <DatePicker 
                                selected={startDate} 
                                onChange={date => setStartDate(date)} 
                                className="form-control" id="dob" aria-describedby="dob" 
                            />
                            <small id="dob" className="form-text text-muted">format is dd-mm-yyyy.</small>
                        </div>
                        <div className="form-group col-sm-6">
                            <label for="fullname">Incorporation / Registration number <span>*</span></label>
                            <input type="text" className="form-control" id="fullname" aria-describedby="fullname" />
                            <small id="fullname" className="form-text text-muted">RC number</small>
                        </div>
                    </div>
                    <div className="form-group">
                        <label for="occupation">Industry Sector / Business Type <span>*</span></label>
                        <select className="form-control" id="occupation">
                            <option>select your sector</option>
                        </select>
                        {/*<input type="text" className="form-control" id="occupation" aria-describedby="occupation" />*/}
                    </div>
                    <div className="form-group">
                        <label for="address">Business Address<span>*</span></label>
                        <input type="text" className="form-control" id="address" aria-describedby="address" />
                    </div>
                    <div className="form-group">
                        <label for="state">State <span>*</span></label>
                        <select className="form-control" id="state">
                            <option>select state</option>
                        </select>
                        {/*<input type="text" className="form-control" id="occupation" aria-describedby="occupation" />*/}
                    </div>
                    <div className="form-group">
                        <label for="phone">Business number <span>*</span></label>
                        <input type="tel" className="form-control" id="phone" aria-describedby="phone" />
                        <small id="phone" className="form-text text-muted">format +234XXXXXXXXXXX.</small>
                    </div>
                    <div className="form-group">
                        <label for="email">Email <span>*</span></label>
                        <input type="email" className="form-control" id="email" aria-describedby="email" />
                    </div>
                    <div className="form-group">
                        <label for="tin">Tax Identification Number <span>*</span></label>
                        <input type="tel" className="form-control" id="tin" aria-describedby="tin" />
                    </div>
                    <div className="form-group">
                        <p>Type of ID<span>*</span></p>
                        <div className="form-check">
                            <input className="form-check-input" type="radio" name="id" id="id1" value="int Passport" />
                            <label className="form-check-label" for="id1">
                                International passport
                            </label>
                        </div>
                        <div className="form-check">
                            <input className="form-check-input" type="radio" name="gender" id="id2" value="Drivers License" />
                            <label className="form-check-label" for="id2">
                                Drivers License
                            </label>
                        </div>
                        <div className="form-check">
                            <input className="form-check-input" type="radio" name="gender" id="id3" value="National ID" />
                            <label className="form-check-label" for="id3">
                                National ID
                            </label>
                        </div>
                        <div className="form-check">
                            <input className="form-check-input" type="radio" name="gender" id="gender2" value="Voters Card" />
                            <label className="form-check-label" for="id4">
                                Voters Card
                            </label>
                        </div>
                    </div>
                    
                    <div className="row">
                        <div className="form-group col-sm-6">
                            <label for="idNumber">ID number <span>*</span></label>
                            <input type="number" className="form-control" id="idNumber" aria-describedby="idNumber" />
                        </div>
                        <div className="form-group col-sm-6">
                            <label for="idcard">Upload Image of ID <span>*</span></label>
                            <input type="file" className="form-control-file" id="idcard" />
                        </div>
                    </div>
                    <div className="row">
                        <div className="form-group col-sm-6">
                            <label for="issued">Issued date <span>*</span></label>
                            <DatePicker 
                                selected={startDate} 
                                onChange={date => setStartDate(date)}
                                className="form-control" id="issued" aria-describedby="issued"
                                
                            />
                            <small id="issued" className="form-text">Date ID was issued.</small>
                        </div>
                        <div className="form-group col-sm-6">
                            <label for="issued">Expiry date <span>*</span></label>
                            <DatePicker 
                                selected={startDate} 
                                onChange={date => setStartDate(date)}
                                className="form-control" id="issued" aria-describedby="issued"
                                
                            />
                            <small id="issued" className="form-text">Date ID will expire.</small>
                        </div>
                    </div>
                    <div className="form-group">
                        <label for="exampleFormControlTextarea1">Any other information?</label>
                        <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                    </div>
                    <p><small>
                        By clicking submit you thereby agree that the information provided is true and complete to the 
                        best of his knowledge and belief and that he undertakes to inform KBL Insurance of any changes therein 
                        immediately.  In the event that the information he provided proves to be untrue and incomplete in any respect, 
                        that the Company shall have no liability under the Insurance.
                    </small></p>

                    <button className="button">Submit</button>
                </form>
            </div>
        </div>
    )
}

export default Form;